
    <template>
      <section class="market-doc-container">
        <h2>Drawer 抽屉</h2>
<h3>基本使用</h3>
<p>支持从上下左右四个方向弹出抽屉</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-radio-group type=&quot;button&quot; v-model=&quot;direction&quot;&gt;
      &lt;mku-radio label=&quot;left&quot;&gt;左侧弹出&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;top&quot;&gt;顶部弹出&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;right&quot;&gt;右侧弹出&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;bottom&quot;&gt;底部弹出&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;
    &lt;mku-button
      type=&quot;primary&quot;
      class=&quot;doc-mt20&quot;
      @click=&quot;handleClick&quot;&gt;
      点击打开抽屉
    &lt;/mku-button&gt;

    &lt;mku-drawer
      v-model=&quot;visible&quot;
      title=&quot;抽屉标题&quot;
      :size=&quot;400&quot;
      :direction=&quot;direction&quot;
      :mask=&quot;true&quot;&gt;
      &lt;div style=&quot;padding: 20px;&quot;&gt;
        &lt;mku-form :label-width=&quot;100&quot;&gt;
          &lt;mku-form-item label=&quot;Text：&quot;&gt;
            &lt;mku-input type=&quot;text&quot; placeholder=&quot;This is a text box.&quot; /&gt;
          &lt;/mku-form-item&gt;
          &lt;mku-form-item label=&quot;Password：&quot;&gt;
            &lt;mku-input type=&quot;password&quot; placeholder=&quot;This is a password box.&quot;/&gt;
          &lt;/mku-form-item&gt;
          &lt;mku-form-item label=&quot;Select：&quot;&gt;
            &lt;mku-select class=&quot;doc-select&quot;&gt;
              &lt;mku-options value=&quot;1&quot;&gt;选项1&lt;/mku-options&gt;
              &lt;mku-options value=&quot;2&quot;&gt;选项2&lt;/mku-options&gt;
              &lt;mku-options value=&quot;3&quot;&gt;选项3&lt;/mku-options&gt;
            &lt;/mku-select&gt;
          &lt;/mku-form-item&gt;
          &lt;mku-form-item label=&quot;Checkbox：&quot;&gt;
            &lt;mku-checkbox&gt;同意&lt;/mku-checkbox&gt;
          &lt;/mku-form-item&gt;
          &lt;mku-form-item&gt;
            &lt;mku-button type=&quot;primary&quot;&gt;提交&lt;/mku-button&gt;
          &lt;/mku-form-item&gt;
        &lt;/mku-form&gt;
      &lt;/div&gt;
    &lt;/mku-drawer&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      visible: false,
      direction: 'right'
    }
  },
  methods: {
    handleClick () {
      this.visible = true
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>自定义title</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button type=&quot;primary&quot; @click=&quot;visible = true&quot;&gt;
      点击打开抽屉
    &lt;/mku-button&gt;

    &lt;mku-drawer v-model=&quot;visible&quot;&gt;
      &lt;div style=&quot;padding: 20px;&quot;&gt;
        内容区域
      &lt;/div&gt;
      &lt;div slot=&quot;title&quot;&gt;
        &lt;mku-badge :value=&quot;8&quot;&gt;
          消息面板
        &lt;/mku-badge&gt;
      &lt;/div&gt;
    &lt;/mku-drawer&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      visible: false
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>多级抽屉</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button type=&quot;primary&quot; @click=&quot;visible = true&quot;&gt;
      点击打开抽屉
    &lt;/mku-button&gt;

    &lt;mku-drawer v-model=&quot;visible&quot; :size=&quot;600&quot; title=&quot;第1个抽屉&quot;&gt;
      &lt;div style=&quot;padding: 20px;&quot;&gt;
        &lt;mku-button type=&quot;primary&quot; @click=&quot;visible2 = true&quot;&gt;
          再打开一个抽屉
        &lt;/mku-button&gt;
      &lt;/div&gt;
    &lt;/mku-drawer&gt;

    &lt;mku-drawer v-model=&quot;visible2&quot; title=&quot;第2个抽屉&quot;&gt;
      &lt;div style=&quot;padding: 20px;&quot;&gt;
        Hello Drawer!
      &lt;/div&gt;
    &lt;/mku-drawer&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      visible: false,
      visible2: false
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>拖拽宽度</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;span&gt;size: {{size}}, 单位px&lt;/span&gt;
    &lt;mku-button
      type=&quot;primary&quot;
      @click=&quot;handleClick&quot;&gt;
      可拖拽宽度的抽屉
    &lt;/mku-button&gt;

    &lt;mku-drawer
      v-model=&quot;visible&quot;
      draggable
      title=&quot;可拖拽宽度的抽屉&quot;
      :size.sync=&quot;size&quot;
      :mask=&quot;true&quot;&gt;
      &lt;div style=&quot;padding: 20px;&quot;&gt;
        通过给size属性的sync写法，可以在拖拽改变宽度时同步更新size值
      &lt;/div&gt;
    &lt;/mku-drawer&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      visible: false,
      size: 300
    }
  },
  methods: {
    handleClick () {
      this.visible = true
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Drawer Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
