<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">direction</td>
<td style="text-align:left">弹出的方向</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>left | top | right | bottom</code></td>
<td style="text-align:left"><code>right</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">duration</td>
<td style="text-align:left">动画执行时间</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>300ms</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">title</td>
<td style="text-align:left">标题</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">抽屉大小尺寸，左右时为宽度，上下时为高度。可以使用<code>sync</code>语法</td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>300px</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">closable</td>
<td style="text-align:left">是否显示关闭按钮</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>true</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">mask</td>
<td style="text-align:left">是否展示蒙层</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>true</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">mask-closable</td>
<td style="text-align:left">点击蒙层是否收起抽屉</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>true</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">draggable</td>
<td style="text-align:left">是否可以拖拽</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Drawer Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
